@import url("https://fonts.googleapis.com/css2?family=Open+Sans&family=Source+Sans+Pro:wght@400;600;700&display=swap");

html,
body,
#root,
.content {
  height: 100%;
  width: 100%;
  font-family: "Source Sans Pro", sans-serif;
}

.app {
  display: flex;
  position: relative;
}
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #e0e0e0;
}

/* Hendle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Hendle of Hover*/
::-webkit-scrollbar-track:hover {
  background: #555;
}
